import { Notification} from "element-ui";

export const notificationMixin = {
  methods:{
    warning(){
      Notification({
        title: '',
        message: '',
        type: 'warning',
        position : 'bottom-right'
      })
    },
    success(){
      Notification({
        title: '',
        message: '',
        type: 'success',
        position: 'bottom-right'
      })
    },
    error(){
      Notification({
        title: '',
        message: ',',
        type: 'error',
        position: 'bottom-right'
      })
    }
  }
}